import React, { Fragment, PureComponent } from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import InputLabel from '@material-ui/core/InputLabel';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import { format } from 'date-fns';
// import {moment} from 'moment';

import { DateTimePicker, InlineDateTimePicker } from 'material-ui-pickers';

const styles = theme => ({
  container: {
    display: 'flex',
    // flexWrap: 'wrap',
    padding: theme.spacing.unit,
    marginBottom: 1,
    // justifyContent: 'center',
    width: "100%",
    height: '80%',
  },
  demo: {
    // height: '100%',
    width: "100%",
    padding: theme.spacing.unit, 
  
  },
  // textField: {
  //   marginLeft: theme.spacing.unit,
  //   marginRight: theme.spacing.unit,
  //   width: 220,
  //   fontsize: 14,
  // },
  button: {
    margin: theme.spacing.unit,
  },
  leftIcon: {
    marginRight: theme.spacing.unit,
  },
  rightIcon: {
    marginLeft: theme.spacing.unit,
  },
  iconSmall: {
    fontSize: 20,
  },
  label: {
    padding: 5,
    marginBottom: theme.spacing.unit,
  },
  msgDiv: {
    width: '100%', display: 'flex',
  },
  txtField: {

  },
  paper: {
    // height: "99%",
    // width: "100%",
  },
  cheight:   {
    height: "80%", 
    overflowY: 'scroll',
    // width: "100%",
  },
  name:   {
    color: "blue", 
  },
  message:   {
    color: "red",  
  },

});

class ChatPage extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      open: false,
      messages:[],
      message:{msg:"", dt:''},
    };
 
  }
 
  getTime(){
    
    function pad(number) {
      if (number < 10) {
        return '0' + number;
      }
      return number;
    };


    let dtnow = new Date();
    let dt = dtnow.getUTCFullYear() +
      '-' + pad(dtnow.getUTCMonth() + 1) +
      '-' + pad(dtnow.getUTCDate()) +
      'T' + pad(dtnow.getUTCHours()) +
      ':' + pad(dtnow.getUTCMinutes()) +
      ':' + pad(dtnow.getUTCSeconds());
    // '.' + (this.getUTCMilliseconds() / 1000).toFixed(3).slice(2, 5) +
    // 'Z';

    // dt = dt.toISOString();
    return dt;
  }

  handleChangeMessage = event => {
    this.setState({ message: {msg: event.target.value ,  dt: this.getTime()} });
  } 
  


  handleSendMessage = (event) => { 
    this.setState({ messages: [...this.state.messages , this.state.message] });
  };

  render() {
    const {messages, message } = this.state;
    const { classes } = this.props; 
    
   


  
    const chatList = messages.map((msg, count) => {
      return <div  key ={count}> <span>{msg.dt}</span><br/><span className={classes.name }>You:</span> <br/> <span className={classes.message }>{msg.msg}</span> </div>
    })

    return (
      <form className={classes.container } noValidate>
       <div className={classes.demo}>

        
          <Paper className={classes.cheight}  >
         
          {chatList}
          </Paper>
          {/* </div> */}
          {/* <Fragment> */}
          <Paper   >
          <div className={classes.msgDiv}>  
            <TextField
              id="Message"
              label="Message"
              style={{ margin: 8 }}
              value={message.msg}
              placeholder="Halo"
              helperText=""
              fullWidth
              margin="normal"
              onChange={this.handleChangeMessage}
              InputLabelProps={{
                shrink: true,
              }}
            />
            {/* <div className={classes.msgDiv}> */}
            <Button color="secondary" onClick={  this.handleSendMessage} variant="contained" className={classes.button}>  Send </Button>
         
          {/* </Fragment> */}
          </div> 
          </Paper>
          </div>
      </form>
    );
  }
}

ChatPage.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(ChatPage);