import React from 'react';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import StrengthChart from './strengthchart';
import SpeedChart from './speedchart';
import DatePickers from './datepickers';

import { withStyles } from '@material-ui/core/styles';
import { connect } from 'react-redux'
// import * as vehicleActions from "./actions/vehicles";
// import { bindActionCreators } from "redux";

const styles = theme => ({
  root: {
    flexGrow: 1,
    display: 'flex',
    // padding: theme.spacing.unit * 1,
    // padding: theme.spacing.unit * 3,
    height: '100vh',
    overflow: 'auto',
    width: '100%',
  },
  demo: {
    // height: '100%',
    padding: theme.spacing.unit,
  },
  center: {
    textAlign: 'center',
  },
  left: { textAlign: 'center', },
  paper: {
    padding: theme.spacing.unit * 2,
    height: '27%',
    textAlign: 'left',
    color: theme.palette.text.secondary,
    margin: '0 2px 2px ',
     
  },
  control: {
    padding: theme.spacing.unit * 2,
  },
  card: {
    minWidth: 2,
  },
  bullet: {
    display: 'inline-block',
    margin: '0 2px',
    transform: 'scale(0.8)',
  },
  title: {
    fontSize: 12,
  },
  pos: {
    marginBottom: 12,
  },
  toolbar: {
    paddingRight: 24, // keep right padding when drawer closed
  },
  toolbarIcon: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'flex-end',
    padding: '0 8px',
    ...theme.mixins.toolbar,
  },

  chartContainer: {
    marginLeft: -22,
  },
  tableContainer: {
    width: '100%',
    height: '70%',
  },
  h5: {
    marginBottom: theme.spacing.unit * 2,
  },
  button: {
    margin: theme.spacing.unit,
    margin: '0 0 0 12px',
  },
});
class AnalyticsPage extends React.Component {

  constructor(props) {
    super(props);

  }

  render() {
    const { pos, classes } = this.props;
    let ts = pos;

    return ( 

      <Paper styles={{border:'none'}} > 
        {/* <Paper  >
                    <DatePickers submit={this.props.handleSubmit} realtimeclick={this.handleRealTimeClick} />
                  </Paper> */}
        <Paper className={classes.paper}>
          <Typography variant="subtitle2" gutterBottom   >
            Signal Strength
            </Typography>
          <StrengthChart pos={ts} />
        </Paper>
        <Paper className={classes.paper}>
          <Typography variant="subtitle2" gutterBottom  >
            Speed
            </Typography>
          <SpeedChart pos={ts} />
        </Paper>
        <Paper className={classes.paper}>
          <Typography variant="subtitle2" gutterBottom  >
            Acceleration
                  </Typography>
          <SpeedChart pos={ts} />
        </Paper>
      </Paper>

    );
  }
}


const mapStateToProps = (state) => {
  return {
    vehicles: state.vehicles
  }
}

export default withStyles(styles)(connect(mapStateToProps)(AnalyticsPage));

