import React from 'react'; 
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid'; 
import { connect } from 'react-redux' 

import "../node_modules/video-react/dist/video-react.css";
import { Player,BigPlayButton } from 'video-react';

const styles = theme => ({
  root: {
    flexGrow: 1,
    display: 'flex',
    padding: theme.spacing.unit * 1,
    // padding: theme.spacing.unit * 3,
    height: '100vh',
    overflow: 'auto',
    width: '100%',
  },
  demo: {
    // height: '100%',
    padding: theme.spacing.unit,
  },
  center: {
    textAlign: 'center',
  },
  left: { textAlign: 'center', },
  paper: {
    padding: theme.spacing.unit * 2,
    height: '27%',
    textAlign: 'left',
    color: theme.palette.text.secondary,
    margin: '0 2px 2px ',
  },
  control: {
    padding: theme.spacing.unit * 2,
  },
  card: {
    minWidth: 2,
  },
  bullet: {
    display: 'inline-block',
    margin: '0 2px',
    transform: 'scale(0.8)',
  },
  title: {
    fontSize: 12,
  },
  pos: {
    marginBottom: 12,
  },
  toolbar: {
    paddingRight: 24, // keep right padding when drawer closed
  },
  toolbarIcon: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'flex-end',
    padding: '0 8px',
    ...theme.mixins.toolbar,
  },

  chartContainer: {
    marginLeft: -22,
  },
  tableContainer: {
    width: '100%',
    height: '70%',
  },
  h5: {
    marginBottom: theme.spacing.unit * 2,
  },
  button: {
    margin: theme.spacing.unit,
    margin: '0 0 0 12px',
  },
});

class MediaPage extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      pos: 0.
    };
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.pos !== this.props.pos) {
      this.setState({ pos: nextProps.pos });
    }

  }

  render() {
    const { pos, classes } = this.props;
    let ts = pos;

    return (


         <Paper   >
          <Paper style={{  width : '100%', padding: 10  }} >
          <Player style={{width : '100%', height:'100%',  }}
      playsInline
      poster="/assets/poster.png"
      src="https://www.mapbox.com/bites/00188/patricia_nasa.mp4"
     > 
     <BigPlayButton position="center" />
     </Player>

     </Paper>
        
        </Paper> 
    );
  }
}


const mapStateToProps = (state) => {
  return {
    vehicles: state.vehicles
  }
}

export default connect(mapStateToProps)(MediaPage);

